export const ProfileOrderTable = () => {
  return (
    <Table className="w-full table-auto bg-white border-separate border-spacing-y-2">
      <TableHeader>
        <TableRow className="bg-whiteOut text-center border border-gray-200">
          <TableHead className="px-2 text-center border border-superSilver text-[15px] leading-[25px] font-medium text-textColor relative">
            <span>Заказы</span>
            <ChevronDown className="w-[16px] h-[16px] absolute right-3 top-1/2 -translate-y-1/2" />
          </TableHead>
          <TableHead className="px-2 text-center border-r border-gray-200 text-[15px] leading-[25px] font-medium text-textColor relative">
            <span>Контрагент</span>
            <ChevronDown className="w-[16px] h-[16px] absolute right-3 top-1/2 -translate-y-1/2" />
          </TableHead>
          <TableHead className="px-2 text-center border-r border-gray-200 text-[15px] leading-[25px] font-medium text-textColor">
            Доставка
          </TableHead>
          <TableHead className="px-2 text-center border-r border-gray-200 text-[15px] leading-[25px] font-medium text-textColor">
            Статус оплаты
          </TableHead>
          <TableHead className="px-2 text-center border-r border-gray-200 text-[15px] leading-[25px] font-medium text-textColor relative">
            <span>Сумма</span>
            <ChevronsUpDown className="w-[19px] h-[19px] absolute right-3 top-1/2 -translate-y-1/2" />
          </TableHead>
          <TableHead className="px-2 text-center border-gray-200 text-[15px] leading-[25px] font-medium text-textColor relative"></TableHead>
        </TableRow>
      </TableHeader>
      <TableBody>
        {orders?.map((order: OrderResponse) => (
          <TableRow key={order.id} className="">
            <TableCell className="py-[7px] px-[10px]">asdsad</TableCell>
          </TableRow>
        ))}
        {/* Agar qo‘shimcha statik qator bo‘lsa: */}
        <TableRow className="">
          <TableCell className="py-[7px] px-[10px]">asdasd</TableCell>
        </TableRow>
      </TableBody>
    </Table>
  );
};

export default ProfileOrderTable;
